<template>
    <div>
              
        <div v-if="loading">
            <div style="position: absolute;top: 50%;left: 50%;transform: translateX(-50%) translateY(-50%);">
                <div class="animated bounce infinite text-4xl font-bold">
                    Loading ...
                </div>
            </div>
        </div>
        <div v-else>

        <div v-if="showTafsir" class="anim shadow-lg" style="width:100vw;position:fixed;left:0;height:30vh;z-index:100;background:white;background:black;opacity:0.3"  :style="`${showTafsir?'top:0px;':'top:-30vh;'}`" @click="showTafsir=false">
        </div>
        <div v-if="showTafsir" class="anim shadow-lg" style="position:fixed;left:0;height:70vh;z-index:100;background:white;"  :style="`${showTafsir?'bottom:0px;':'bottom:-70vh;'}`">
            <div class="shadow-md text-justify text-sm p-3">
                   <div class="sm-form float-right ">
                        <label for="lebar">Ukuran Ayat : </label>
                        <select class='form-control' style="width:100px;" v-model="pilihanLebar">
                            <option v-for="(item, index) in lebar" :key="index" :value="item">{{index+1}}</option>
                        </select>
                    </div>
                <p class="font-bold text-lg">
                    Ayat : {{tafsir+1}}
                </p>
                <p class="font-bold text-lg">
                   Tafsir Source : Kemenag
                </p>
                <hr>
                <div
                class="text-justify text-lg p-4"
                style="height:300px;overflow:scroll;"
                 v-html="td.tafsir.id">
                </div>

            </div>
        </div>
        <div class="row justify-content-center">
          <div class="col-md-10">
                <div class="col-12 p-3">
                 
                    <p class="text-lg float-right">(<span class="font-italic">{{td.revelation.id}}</span>)
                    <br>
                    ayat : {{td.numberOfVerses}}
                    </p>
                    <div class="d-block sm:d-none">
                        <br>
                        <br>
                        <br>
                    </div>
                    <span class="text-4xl">Surat {{$route.query.nama_surat}} </span><span class="text-4xl font-italic font-bold" style="font-family: 'Amiri', serif;line-height: 2.0;">{{td.name.short}} <br> </span>
                    <p class="text-lg">
                        Arti nama surat : (<span class="font-italic">{{td.name.translation.id}}</span>)
                    </p>
                </div>
                  <br>
                <div class="input-group mb-2">
                  <div class="input-group-prepend">
                      <div class="input-group-text"><img src="https://upload.egov.balangankab.go.id/svg/pen.svg" 
                           class="d-inline" style="height:20px;width:20px;" alt=""></div>
                  </div>
                  <input type="text" class="form-control py-0" id="pencarian" placeholder="Cari No Ayat ..." v-model="cariayat">
              </div>
              <div class="shadow-md rounded-lg p-3 mt-5 cursor-pointer" 
              @click="showTafsir=true;tafsir=index" v-for="(item, index) in ayat" :key="index+'surat'">
              <div class="row sm:p-3">
                  <div class="col-1 font-bold text-lg">{{item.number.inSurah}}.</div>
                  <div class="col-11 text-right " :class="`${pilihanLebar}`" style="font-family: 'Amiri', serif;line-height: 2.0;line-height:2.0;">
                      <div class="float-right ">
                       <audio controls>
                            <source :src="`${item.audio.secondary[0]}`" type="audio/mpeg">
                            Your browser does not support the audio element.
                        </audio>
                      </div>
                      <br>
                      <br>
                      {{item.text.arab}}</div>
                  <div class="col-12 p-3">
                      <p v-html="item.translation.id" class="text-lg">
                      </p>
                     
                  </div>
              </div>
              </div>
          </div>
        </div>
        </div>
    </div>
</template>
<script>
let id = window.location.href.toString().split('=')[2];
import axios from 'axios'
export default{
    layout:'app_islamic',
    data(){
        return{
            datanya:[],
            showTafsir:false,
            tafsir:{},
            cariayat:'',
            loading:true,
            lebar:[
                'text-lg',
                'text-xl',
                'text-2xl',
                'text-4xl',
                'text-5xl',
            ],
            pilihanLebar:'text-4xl'
        }
    },
    watch:{
        pilihanLebar(){
            this.showTafsir=false;
        }
    },
    computed:{
        td(){
            let that =this;
            let data =this.datanya;
            return data;
        },
        ayat(){
            let that =this;
            let data =this.datanya.verses;
            data=data.filter(e=>{
                if(e.number.inSurah.toString().indexOf(this.cariayat)!=-1){
                    return e;
                }
            })
            // data=data.map((e,i)=>{
            //     return{
            //         no:i+1,
            //         ...e
            //     }
            // })
            return data;
        },
    },
    methods: {
        getSurah(){
            // axios.get(`https://akbarmaliki.github.io/surah/${this.$route.query.surat}.json`).then(res=>{
            //     this.datanya=res.data;
            //     console.log(res.data)
            //     this.$forceUpdate();
            // })
            //TAMBAH DI PUBLIC FOLDER SEMUA FILE JSON
            axios.get(`islamic/${this.$route.query.surat}.json`).then(res=>{
                this.datanya=res.data.data;
                // console.log(res.data.data)
                this.loading=false;
                this.$forceUpdate();
            })
        }
    },
    mounted() {
        this.getSurah();
    },
      metaInfo: {
        title: `Baca Al Qur'an`,
        link: [
         { rel: 'preconnect', href: 'https://fonts.googleapis.com' },
         { rel: 'preconnect', href: 'https://fonts.gstatic.com' },
         { rel: 'stylesheet', href: 'https://fonts.googleapis.com/css2?family=Amiri&display=swap' },
          { rel: 'stylesheet', href: 'https://fonts.googleapis.com/css2?family=Dancing+Script&display=swap' },
         { rel: 'stylesheet', href: 'https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.3/css/all.min.css' },
        ],
    },
}
</script>